fix}-auto {\n                    margin-bottom: auto !important;\n                }\n\n                .ml#{$infix}-auto {\n                    margin-left: auto !important;\n                }\n\n                .mx#{$infix}-auto {\n                    margin-right: auto !important;\n                    margin-left: auto !important;\n                }\n\n                .my#{$infix}-auto {\n                    margin-top: auto !important;\n                    margin-bottom: auto !important;\n                }\n            }\n        }\n    }\n}\n\n// -----------------------------------------------------------------------------------------------------\n// @ Border helpers\n// -----------------------------------------------------------------------------------------------------\n$border-style: 1px solid rgba(0, 0, 0, 0.12);\n\n.border,\n.b {\n    border: $border-style;\n}\n\n.border-top,\n.bt {\n    border-top: $border-style;\n}\n\n.border-right,\n.br {\n    border-right: $border-style;\n}\n\n.border-bottom,\n.bb {\n    border-bottom: $border-style;\n}\n\n.border-left,\n.bl {\n    border-left: $border-style;\n}\n\n.border-horizontal,\n.bx {\n    border-left: $border-style;\n    border-right: $border-style;\n}\n\n.border-vertical,\n.by {\n    border-top: $border-style;\n    border-bottom: $border-style;\n}\n\n// -----------------------------------------------------------------------------------------------------\n// @ Border radius helpers\n// -----------------------------------------------------------------------------------------------------\n.border-radius-100 {\n    border-radius: 100%;\n}\n\n.border-radius-2 {\n    border-radius: 2px;\n}\n\n.border-radius-4 {\n    border-radius: 4px;\n}\n\n.border-radius-8 {\n    border-radius: 8px;\n}\n\n.border-radius-16 {\n    border-radius: 16px;\n}\n\n// -----------------------------------------------------------------------------------------------------\n// @ Cursor helpers\n// ------------------------------------------------